import axios from 'axios';
let tokenjwt = localStorage.getItem('token');

export default class AppartementService {

    getApp(residenceId) {
      
        let respo;
        let tokenjwt = localStorage.getItem('token');
        try {
          const response =  axios.get(`${process.env.VUE_APP_HOST_BACKEND}/appartement/view/${residenceId}`, {
            headers: {
              Authorization: `${tokenjwt}`,
            },
          });
          respo=response;
        } catch (error) {
          console.error(error);
        }
        return respo;

  }
  saveAppartement(body,Residenceid) {  
    let resp = {
      type: null,
      msg: null,
      summary: null
    }
  
    const request = axios.post(`${process.env.VUE_APP_HOST_BACKEND}/appartement/add/${Residenceid}`, body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Résidence ajoutée avec succès";
        resp.summary = "Action Terminée";
      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
      return resp;
    });
  
    return request;
  }
  updateAppartement(Bloc,residence) {
    
    let resp = {
      type: null,
      msg: null,
      summary: null
    }
    let body={
      bisteg: Bloc.isteg,
      bisonede: Bloc.isonede,
      bigaz: Bloc.igaz  ,
      btype: Bloc.type,
      numappartement  : Bloc.numappartement,
      nbchambre:Bloc.nbchabre,
      newblocid:Bloc.newblocid,
      newetageid:Bloc.newetageid,
      newbtype:Bloc.newbtype,
      prix:Bloc.Prix,
      cotion:Bloc.Cotion,
    }
    const request = axios.put(`${process.env.VUE_APP_HOST_BACKEND}/appartement/update/${residence}/${Bloc.blocid}/${Bloc.etageid}/${Bloc.appartmentId}`, body, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
      if (response.data.success){
        resp.type = "success";
        resp.msg = "Résidence ajoutée avec succès";
        resp.summary = "Action Terminée";
      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
      return resp;
    });

    return request;
  }
  deleteAppartement(data,residence) {
   
    let resp = {
      type: null,
      msg: null,
      summary: null
    }
    const request = axios.delete(`${process.env.VUE_APP_HOST_BACKEND}/appartement/delete/${residence}/${data.blocId}/${data.etageid}/${data.appartmentid}`, {
      headers: {
        Authorization: `${tokenjwt}`,
      },
    })
    .then(response => {
     
      if (response.data.success){

        resp.type = "success";
        resp.msg = "Résidence ajoutée avec succès";
        resp.summary = "Action Terminée";
      }
      else{
        resp.type = "error";
        resp.msg = response.data.msg;
        resp.summary = "ERREUR";
      }
      return resp;
    })
    .catch(error => {
      alert('Wrong user account ' +error )
      return resp;
    });

    return request;

  }
 
}
