<script setup>
import { ref, onBeforeMount ,watchEffect   } from 'vue';
import AppartementService from '../../service/servicebiens/AppartementService';
import { useToast } from 'primevue/usetoast';
import { FadeInOut } from 'vue3-transitions'

import { useRoute } from 'vue-router';
const appatmentservice = new AppartementService();
const AppartmentData = ref(null);
const staticObject  = ref(null);

const route = useRoute();  
const toast = useToast();
const Residenceid = route.params.id;
const dt = ref(null);

const AppartmentaddDialog = ref(false);
const deleteAppatementDialog = ref(false);
const visibleSidebarInfo = ref(false);

const Appartmentadd = ref({});
const submitted = ref(false);
const NbrueDropDown = ref(null);
const NbrueValues = ref([
    {id: 1, name: '1'},
    {id: 2, name: '2'},
    {id: 3, name: '3'},
    {id: 4, name: '4'},
    {id: 5, name: '5'},
    {id: 6, name: '6'},
    {id: 7, name: '7'},
    {id: 8, name: '8'},
    {id: 9, name: '9'},
    {id: 10, name: '10'},
    
]);

let shownbchambre=ref(false)

let updateprogressindex = ref(1);
let stepone = ref(true);
let steptwo = ref(false);

let nbLit = ref(0);


let Etagenametoremove = ref(false);
let deleteappartmentdata = ref("");

let Appartmenttitledialog=ref("")
let Appartmenttitledialogtype=ref("")

let BlocdropdownValue = ref(null);
let BlocdropdownValues = ref(null);

let EtagenumberdropdownValue = ref(null);
let EtagenumberdropdownValues = ref(null);

let TypeAppdriodownvalue=ref(null)
let TypeAppdriodownvalues = ref([
    {id:"AF",name:"Appartement Familial"},
    {id:"AC",name:"Appartement à lit"},
    
]);

const refreshComponent = ()=>{
    readResidence(Residenceid);

}
const readResidence = async (id) => {
    const response = await appatmentservice.getApp(id);
    const blocsList = [];
    response.data.STATICOBJECT.forEach((staticObject) => {
        const name = staticObject.blocname;
        const id = staticObject.blocid;
        blocsList.push({ name, id });
    });
    AppartmentData.value = response.data.appartements;
    staticObject.value = response.data.STATICOBJECT;
    BlocdropdownValues.value = blocsList;
};

const openNew = () => {
    stepone.value = true;
    steptwo.value = false;


    shownbchambre.value=false
    Appartmenttitledialog.value = "Ajouter un appartement";
    Appartmenttitledialogtype.value = "Add";
    Appartmentadd.value = {};
    submitted.value = false;
    AppartmentaddDialog.value = true;
    TypeAppdriodownvalue.value=null
    BlocdropdownValue.value=null
    EtagenumberdropdownValue.value=null
    setTimeout(() => {
       
       let liststeps = document.getElementsByClassName("p-steps-item");
       liststeps[0].classList.add("p-highlight");
       Appartmenttitledialog.value="Ajouter un appartement";

    }, 50);
};
const hideDialog = () => {
    AppartmentaddDialog.value = false;
    submitted.value = false;
    TypeAppdriodownvalue.value=null
    BlocdropdownValue.value=null
    EtagenumberdropdownValue.value=null
};

const exportCSV = () => {
    dt.value.exportCSV();
};

async function getEtagesFromBloc(bloc,etageid) {
    const etages = staticObject.value.find((bloctofind) => bloctofind.blocid == bloc.id);
    const etagedata=etages.etagesData
    let pushdata=[]
    etagedata.forEach((data) => {
        if (data.Numetage===0){
            let dropdowndata={
            id:data.EtageId,
            name:"etage RDC"

            }
            pushdata.push(dropdowndata) 
        }else{    
            
            let dropdowndata={
                id:data.EtageId,
                name:"etage "+data.Numetage
                
            }
            pushdata.push(dropdowndata)
        }
    });
    EtagenumberdropdownValues.value=pushdata

}

const checkinputNumapp = (bloc,value) => {
    const etages = staticObject.value.find((bloctofind) => bloctofind.blocid == bloc.id);
    const etagedata=etages?.etagesData
    

}


const saveAppartment = async () => {
    
    submitted.value = true;
    let data ;
    if( TypeAppdriodownvalue.value.id=="AF"){
        data = {
            ...Appartmentadd.value,
            blocid: BlocdropdownValue.value.id,
            etagenumber: EtagenumberdropdownValue.value.id,
            btype:TypeAppdriodownvalue.value.id,


            
        };
    }
    else{        

        data = {
            ...Appartmentadd.value,
            blocid: BlocdropdownValue.value.id,
            etagenumber: EtagenumberdropdownValue.value.id,
            btype:TypeAppdriodownvalue.value.id,
            

            nbchambre:NbrueDropDown.value.id

            
        };

    }
    const response = await appatmentservice.saveAppartement(data,Residenceid);
    toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 });
    hideDialog();
    refreshComponent();
    stepone.value=true
    steptwo.value=false
    updateprogressindex.value=1

};

const editAppartment = async (data) => {
    stepone.value=true
    steptwo.value=false
    updateprogressindex.value=1
    let bloc= {
        id:data.blocid,
        name:data.blocname
    }
    let typeapp={}
    if(data.type=="AF"){
        typeapp={
            id:"AF",
            name:"Appartement Familial"
        }
    }else{
        typeapp={
            id:"AC",
            name:"Appartement à lit"
        }

    }
    
    TypeAppdriodownvalue.value=typeapp
    BlocdropdownValue.value=bloc
    await getEtagesFromBloc(bloc,data.etageid)
    Appartmenttitledialog.value = "Modifier un appartement";
    Appartmenttitledialogtype.value = "edit";

    Appartmentadd.value = data;
    submitted.value = false;
    AppartmentaddDialog.value = true;

    if (data.etagenumber==0){
        data.etagenumber="RDC"
    }

    EtagenumberdropdownValue.value ={
        id:data.etageid,
        name:"etage "+data.etagenumber
    }

}

const ViewApparmtentDetails = (data) => {

    visibleSidebarInfo.value=true
    Appartmentadd.value = { ...data };

    Appartmentadd.value.chambrecounter=0
    data.chambredetails.forEach((chambre) => {
        Appartmentadd.value.chambrecounter+=1
    })
   
}
const updateAppartment = async ( ) =>{
    let data;
    if (TypeAppdriodownvalue.value.id=="AF"){
        data = {
            ...Appartmentadd.value,
            newblocid: BlocdropdownValue.value.id,
            newetageid: EtagenumberdropdownValue.value.id,
            newbtype:TypeAppdriodownvalue.value.id,
        };
    }
    else{
        data = {
            ...Appartmentadd.value,
            newblocid: BlocdropdownValue.value.id,
            newetageid: EtagenumberdropdownValue.value.id,
            newbtype:TypeAppdriodownvalue.value.id,
            nbchambre:NbrueDropDown.value.id

        };
    }
    await appatmentservice.updateAppartement(data,Residenceid).then((response) => {
        toast.add({ severity: response.type, summary: response.summary, detail: response.msg, life: 3000 });
        hideDialog();
        refreshComponent();
    });
    
}
const confirmDeleteAppartment =  (data) => {
    Etagenametoremove=data.blocname + " Etage: " + data.etagenumber + " Appartement: " + data.numappartement;
    deleteappartmentdata.value=data
    deleteAppatementDialog.value = true;
};
const deleteApptement = async () => {
    let data ={
        blocId:deleteappartmentdata.value.blocid,
        etageid:deleteappartmentdata.value.etageid,
        appartmentid:deleteappartmentdata.value.appartmentId
    }
    const deleteresult= await appatmentservice.deleteAppartement(data,Residenceid)

    deleteAppatementDialog.value = false;
    deleteappartmentdata.value = "";
    toast.add({ severity: deleteresult.type, summary: deleteresult.summary, detail: deleteresult.msg, life: 3000 });
    refreshComponent()
}
const changetype = () => {
    if(TypeAppdriodownvalue.value.id=="AF"){
       shownbchambre.value=false
    }else{
        shownbchambre.value=true
    }
}

const nestedRouteItems = ref([
    {
        label: 'Location',
        to: ''
    },
    {
        label: 'Referances',
        to: ''
    },


]);

let nextstep = () => {
  
  let liststeps = document.getElementsByClassName("p-steps-item");
  if (updateprogressindex.value>=2){
      updateprogressindex.value=2
      liststeps[1].classList.add("p-highlight");
      liststeps[0].classList.remove("p-highlight");
  }
  else{
      for (let i = 0; i < liststeps.length; i++) {
          if (i == updateprogressindex.value) {
              liststeps[i].classList.add("p-highlight");
          } else {
              liststeps[i].classList.remove("p-highlight");
          } 
      }
  }

  updateprogressindex.value++

  switch (updateprogressindex.value) {
      case 1:
          setStep("Ajouter un nouveau Locataire", true,false );
      break;
      case 2:
          setStep("Ajouter Les Referances", false, true);
      break;

  }
};
let previousstep = () => {
  updateprogressindex.value--;

let liststeps = document.getElementsByClassName("p-steps-item");
if (updateprogressindex.value <= 1) {
  updateprogressindex.value = 1;
  liststeps[0].classList.add("p-highlight");
  liststeps[1].classList.remove("p-highlight");
} else {
  for (let i = 0; i < liststeps.length; i++) {
    if (i + 1 === updateprogressindex.value) {
      liststeps[i].classList.add("p-highlight");
    } else {
      liststeps[i].classList.remove("p-highlight");
    }
  }
}


switch (updateprogressindex.value) {
      case 1:
          setStep("Ajouter un nouveau Locataire", true,false );
      break;
      case 2:
          setStep("Ajouter Le paiment", false, true);
      break;

  }
};
let setStep = (title, stepOne, stepTwo) => {

Appartmenttitledialog.value = title;
stepone.value = stepOne;
steptwo.value = stepTwo;

};


watchEffect(() => {
    if (route.name == "Appartement") {
        readResidence(route.params.id)
    }
});

onBeforeMount( async () => {
    readResidence(Residenceid)
});

</script>

<template>
    <div class="grid">
        <div class="col-12">
            <div class="card">
                <Toast />
                <Toolbar class="mb-4">
                    <template v-slot:start>
                        <div class="my-2">
                            <Button label="Ajouter" icon="pi pi-plus" class="p-button-success mr-2" @click="openNew" />
                        </div>
                    </template>
                    
                    <template v-slot:end>
                        <Button label="Exporter" icon="pi pi-upload" class="p-button-help" @click="exportCSV($event)" />
                    </template>
                </Toolbar>
                <DataTable
                    ref="dt"
                    :value="AppartmentData"
                    dataKey="name"
                    :paginator="true"
                    :rows="10"
                    paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                    :rowsPerPageOptions="[5, 10, 25]"
                    currentPageReportTemplate="Showing {first} to {last} of {totalRecords} Appartments"
                    responsiveLayout="scroll"
                    sortField="type" :sortOrder="-1" 
                >    
                    <Column field="type" header="Type d'appartement"  headerStyle="width:10%; min-width:15rem;" :sortable="true">
                        <template #body="slotProps">
                            <a v-if="slotProps.data.type === 'AC'" style="color: #3b82f6;">Appartement à Lit</a>
                            <a v-else-if="slotProps.data.type === 'AF' && slotProps.data.Isrent" style="color: #ef4444;">Appartement Famillial</a>
                            <a v-else-if="slotProps.data.type === 'AF' && !slotProps.data.Isrent" style="color: #22c55e;">Appartement Famillial</a>
                        </template>
                    </Column>
                    <Column field="blocname" header="Nom Bloc"  headerStyle="width:5%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.blocname }}
                        </template>
                    </Column>
                    <Column field="etagenumber" header="Num. etage"  headerStyle="width:15%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.etagenumber ==0 ? "RDC" : slotProps.data.etagenumber }}
                        </template>
                    </Column>
                    <Column field="numappartement" header="Num App." headerStyle="width:15%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps">
                            {{ slotProps.data.numappartement }}
                        </template>
                    </Column>

                    <Column field="Prix" header="Prix"  headerStyle="width:10%; min-width:10rem;" :sortable="true">
                        <template #body="slotProps" >
                            <div v-if="slotProps.data.type == 'AF'">
                                <a v-if="slotProps.data.Isrent" style="color: #ef4444;">{{ slotProps.data.Prix }} DT</a>
                                <a v-else-if=" !slotProps.data.Isrent" style="color: #22c55e;">{{ slotProps.data.Prix }} DT</a>

                                
                            </div>
                            <div v-else>
                                <a style="color: #3b82f6;">Voir Lit</a>
                            </div>
                        </template>
                    </Column>
                    <Column headerStyle="min-width:10rem;">
                        <template #body="slotProps">
                            <Button icon="fas fa-eye"  class="p-button-rounded p-button-secondary mr-2 " @click="ViewApparmtentDetails(slotProps.data)"/>
                            <Button icon="pi pi-pencil" class="p-button-rounded p-button-success mr-2" @click="editAppartment(slotProps.data)" />
                            <Button icon="pi pi-trash"  class="p-button-rounded p-button-warning mt-2" @click="confirmDeleteAppartment(slotProps.data)" />
                        </template>
                    </Column>
                </DataTable>
               
                    <Sidebar v-model:visible="visibleSidebarInfo"  position="right" class="w-full md:w-20rem lg:w-30rem">
                        <h2>About Appartment Num {{  Appartmentadd.numappartement }}</h2>
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">Type D'appartement</label>
                            <InputText autocomplete="off" v-if="Appartmentadd.type == 'AF' " class="input100" value="Appartement Famillial" disabled="disabled"/>
                            <InputText autocomplete="off" v-if="Appartmentadd.type == 'AC' " class="input100" value="Appartement à lit" disabled="disabled"/>

                        </div>

                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">Nom Bloc</label>
                            <InputText autocomplete="off" class="input100" :value="Appartmentadd.blocname" disabled="disabled"/>
                        </div>
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">Etage Numero</label>
                            <InputText autocomplete="off" class="input100" :value="Appartmentadd.etagenumber " disabled="disabled"/>
                        </div>
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">Appartment Num</label>
                            <InputText autocomplete="off" class="input100" :value="Appartmentadd.numappartement" disabled="disabled" />
                        </div>

                        <div v-if="Appartmentadd.Prix">

                            <hr/>

                            <div class="mb-1 lg:col-4 ">
                                <label for="name" class="mb-1">Prix</label>
                                <InputText autocomplete="off" class="input100" :value="Appartmentadd.Prix" disabled="disabled"/>
                            </div>
                            <div class="mb-1 lg:col-4 ">
                                <label for="name" class="mb-1">Cotion</label>
                                <InputText autocomplete="off" class="input100" :value="Appartmentadd.Cotion" disabled="disabled"/>
                            </div>
                        </div>
                        <div v-else>

                            <div class="mb-1 lg:col-4 ">
                                <label for="name" class="mb-1">Nb Chambres</label>
                                <InputText autocomplete="off" class="input100" :value="Appartmentadd.chambrecounter" disabled="disabled"/>
                            </div>
                        </div>
                        <hr/>                        
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">ref Steg</label>
                            <InputText class="input100" :value="Appartmentadd.isteg" disabled="disabled"/>
                        </div>
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">ref Sonede</label>
                            <InputText autocomplete="off" class="input100" :value="Appartmentadd.isonede" disabled="disabled"/>
                        </div>
                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1">ref Gaz</label>
                            <InputText autocomplete="off" class="input100" :value="Appartmentadd.igaz" disabled="disabled"/>
                        </div>
                        <hr/>                        

                        <div class="mb-1 lg:col-4 ">
                            <label for="name" class="mb-1 input100">Cette Appartement est louée?</label>
                            <badge v-if="Appartmentadd.Isrent" severity="danger" size="large" value="oui"></badge>
                            <badge v-else size="large" class="mr-2" severity="success" value="Non"></badge>
                        </div>
                    </Sidebar>
                    <Dialog v-model:visible="AppartmentaddDialog" :style="{ width: '450px' }" :header=Appartmenttitledialog :modal="true" class="p-fluid">
                        <div class="field">
 
                            <Steps :model="nestedRouteItems" :readonly="true" />

                        </div>
                        <FadeInOut entry="left" exit="right" :duration="100">
                            <div v-if="stepone && !steptwo" >

                                <div class="field" >
                                    <label>Type D'appartement</label>
                                    <Dropdown v-model="TypeAppdriodownvalue" :options="TypeAppdriodownvalues" optionLabel="name" placeholder="Select" @change="changetype()"/>
                                </div>
                                <div class="field" >
                                    <label>Choisir Bloc</label>
                                    <Dropdown v-model="BlocdropdownValue" :options="BlocdropdownValues" optionLabel="name" placeholder="Select" @change="getEtagesFromBloc(BlocdropdownValue)"/>
                                </div>
                                <div class="field">
                                    <label>Numero d'etage</label>
                                    <Dropdown v-model="EtagenumberdropdownValue" :options="EtagenumberdropdownValues" optionLabel="name" placeholder="Select" :disabled="!BlocdropdownValue"/>
                                </div>
                                <div class="field" >
                                    <label>Appartment Num</label>
                                    <InputText  autocomplete="off" v-model.trim="Appartmentadd.numappartement" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue" @input="checkinputNumapp(BlocdropdownValue,Appartmentadd.numappartement)"/>
                                </div>
                                <div class="field" v-if="shownbchambre">
                                    <label>Nombre De chambres</label>
                                    <Dropdown v-model="NbrueDropDown" :options="NbrueValues" optionLabel="name" placeholder="Select" />
                                </div>
                                <div class="field " v-if="!shownbchambre">
                                    <label>Prix</label>
                                    <InputNumber  v-model.trim="Appartmentadd.Prix" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue"/>
                                </div>
                                <div class="field " v-if="!shownbchambre">
                                    <label>cautionnement</label>
                                    <InputNumber  v-model.trim="Appartmentadd.Cotion" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue"/>
                                </div>
                               
                            </div>

                        </FadeInOut>
                        <FadeInOut entry="left" exit="right" :duration="100">

                            <div v-if="steptwo && !stepone">
                            
                                <div class="field">
                                    <label>ref Steg</label>
                                    <InputText autocomplete="off"  v-model.trim="Appartmentadd.isteg" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue"/>
                                </div>
                                <div class="field">
                                    <label>ref Sonede</label>
                                    <InputText autocomplete="off"  v-model.trim="Appartmentadd.isonede" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue"/>
                                </div>
                                <div class="field ">
                                    <label>ref Gaz</label>
                                    <InputText  autocomplete="off" v-model.trim="Appartmentadd.igaz" :disabled="!BlocdropdownValue || !EtagenumberdropdownValue"/>
                                </div>
    
                            </div>
                        </FadeInOut>

                        <template #footer>

                            <Button v-if="stepone"   label="Fermer" icon="pi pi-times" class="p-button-text"            @click="hideDialog()"/>
                            <Button v-if="!stepone"  label="Precedant" icon="pi pi-arrow-left" class="p-button-text"    @click="previousstep()"/>                        
                            <Button v-if="stepone"   label="Suivant" icon="pi pi-arrow-right" class="p-button-text"     @click="nextstep()"  />    
                            <Button v-if="Appartmenttitledialogtype == 'Add' && steptwo" label="Ajouter" icon="pi pi-check" class="p-button-text" @click="saveAppartment()"/>
                            <Button v-if="Appartmenttitledialogtype == 'edit' && steptwo" label="Modifier" icon="pi pi-check" class="p-button-text" @click="updateAppartment()"/>

                        </template>
                     </Dialog>
                     <Dialog v-model:visible="deleteAppatementDialog" :style="{ width: '450px' }" header="Confirm" :modal="true">
                        <div class="flex align-items-center justify-content-center">
                            <i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
                            <span v-if="Appartmentadd">Etes vous sur de suprimer  <br> <b>{{ Etagenametoremove }}</b> ?</span>
                        </div>
                        <template #footer>
                            <Button label="No" icon="pi pi-times" class="p-button-text" @click="deleteAppatementDialog = false;Etagenametoremove=null" />
                            <Button label="Yes" icon="pi pi-check" class="p-button-text" @click="deleteApptement()" />
                        </template>
                    </Dialog>
            </div>
        </div>
    </div>
</template>
<style scoped>
    @media screen and (min-width: 992px) {
        .input100{
            width: 355% !important;
            display: block;
        }
    }
    .input100{
        width: 100%;
        display: block;
        color: black;
        opacity: 1 !important;
    }
    .line-seperator{
        border: 3px solid #484bad;
        border-radius: 10px;    
    }
</style>

